"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Slider } from "@/components/ui/slider"
import { Card, CardContent } from "@/components/ui/card"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { ServiceIcon, getServiceLabel } from "@/components/ui/service-icon"
import { MapView } from "@/components/map/map-view"
import { createServiceRequest, createPet } from "@/lib/api"
import { useGeolocation } from "@/lib/use-geolocation"
import type { ServiceType } from "@/lib/types"
import type { User } from "@/app/page"
import { ArrowLeft, Check, MapPin, Calendar, DollarSign, PawPrint } from "lucide-react"

interface CreateRequestFlowProps {
  user: User
  onClose: () => void
  onSubmit: () => void
}

const STEPS = ["Animal", "Service", "Calendrier", "Localisation", "Budget", "Confirmation"] as const

const serviceTypes: { type: ServiceType; description: string }[] = [
  { type: "sitting", description: "Garde à la maison" },
  { type: "walking", description: "Balade quotidienne" },
  { type: "taxi", description: "Transport sécurisé" },
  { type: "emergency", description: "Urgence" },
]

export function CreateRequestFlow({ user, onClose, onSubmit }: CreateRequestFlowProps) {
  const [step, setStep] = useState(0)
  const [pets, setPets] = useState(user?.pets || [])
  const [selectedPet, setSelectedPet] = useState<string | null>(pets[0]?.id ?? null)
  const [serviceType, setServiceType] = useState<ServiceType | null>(null)
  const [dateStart, setDateStart] = useState("")
  const [dateEnd, setDateEnd] = useState("")
  const [price, setPrice] = useState([30])
  const [description, setDescription] = useState("")
  const [submitting, setSubmitting] = useState(false)
  const { coords } = useGeolocation()

  const selectedPetData = pets.find((p) => p.id === selectedPet)
  const [addingPet, setAddingPet] = useState(false)
  const [newPet, setNewPet] = useState({ name: "", species: "dog", breed: "", age: "" })
  const [petError, setPetError] = useState<string | null>(null)

  const canContinue = () => {
    switch (step) {
      case 0:
        return !!selectedPet
      case 1:
        return !!serviceType
      case 2:
        return !!dateStart && !!dateEnd
      case 4:
        return price[0] >= 10
      default:
        return true
    }
  }

  const handleNext = () => {
    if (step < STEPS.length - 1) {
      setStep(step + 1)
    } else {
      handleSubmit()
    }
  }

  const handleBack = () => {
    if (step > 0) {
      setStep(step - 1)
    } else {
      onClose()
    }
  }

  const handleSubmit = async () => {
    setSubmitting(true)
    try {
      const serviceTypeIdMap: Record<ServiceType, number> = { sitting: 1, walking: 2, taxi: 3, emergency: 4 }
      const payload = {
        petId: selectedPet || "",
        serviceTypeId: serviceType ? serviceTypeIdMap[serviceType] : "",
        requestedPrice: price[0],
        location: {
          lat: coords?.lat ?? user?.location?.lat ?? 36.8065,
          lng: coords?.lng ?? user?.location?.lng ?? 10.1815,
          address: user?.location?.address || "Tunis, Tunisia",
        },
      }
      await createServiceRequest(payload)
    } catch (err) {
      console.error("Failed to create request", err)
    } finally {
      setSubmitting(false)
      onSubmit()
    }
  }

  return (
    <div className="min-h-screen bg-background flex flex-col">
      <div className="flex items-center gap-4 p-4 border-b border-border">
        <button onClick={handleBack} className="p-2 hover:bg-secondary rounded-lg">
          <ArrowLeft size={20} />
        </button>
        <div className="flex-1">
          <h1 className="font-semibold">Créer une demande</h1>
          <p className="text-sm text-muted-foreground">
            Étape {step + 1} sur {STEPS.length}
          </p>
        </div>
      </div>

      <div className="flex gap-1 px-4 py-3">
        {STEPS.map((_, i) => (
          <div
            key={i}
            className={`flex-1 h-1 rounded-full transition-colors ${i <= step ? "bg-primary" : "bg-border"}`}
          />
        ))}
      </div>

      <div className="flex-1 p-4 overflow-y-auto space-y-4">
        <h2 className="text-xl font-semibold">{STEPS[step]}</h2>

        {step === 0 && (
          <div className="space-y-3">
            {pets.map((pet) => (
              <button
                key={pet.id}
                onClick={() => setSelectedPet(pet.id)}
                className={`w-full p-4 rounded-xl border-2 transition-all flex items-center gap-4 ${
                  selectedPet === pet.id ? "border-primary bg-primary/5" : "border-border hover:border-primary/50"
                }`}
              >
                <Avatar className="h-14 w-14">
                  <AvatarImage src={pet.image || `/placeholder.svg?height=56&width=56&query=${pet.type} ${pet.breed}`} />
                  <AvatarFallback>{pet.name[0]}</AvatarFallback>
                </Avatar>
                <div className="flex-1 text-left">
                  <p className="font-semibold">{pet.name}</p>
                  <p className="text-sm text-muted-foreground">
                    {pet.breed || pet.type} · {pet.age} ans
                  </p>
                </div>
                {selectedPet === pet.id && (
                  <div className="w-6 h-6 bg-primary rounded-full flex items-center justify-center">
                    <Check size={14} className="text-primary-foreground" />
                  </div>
                )}
              </button>
            ))}

            <button
              className="w-full p-4 rounded-xl border-2 border-dashed border-border hover:border-primary/50 transition-all flex items-center justify-center gap-2 text-muted-foreground"
              onClick={() => setAddingPet((v) => !v)}
              type="button"
            >
              <PawPrint size={20} />
              <span>Ajouter un nouvel animal</span>
            </button>

            {addingPet && (
              <div className="p-4 border rounded-xl space-y-3 bg-secondary/30">
                {petError ? <p className="text-sm text-destructive">{petError}</p> : null}
                <div className="grid grid-cols-2 gap-3">
                  <div className="space-y-1">
                    <Label>Nom</Label>
                    <Input value={newPet.name} onChange={(e) => setNewPet({ ...newPet, name: e.target.value })} />
                  </div>
                  <div className="space-y-1">
                    <Label>Type</Label>
                    <Input value={newPet.species} onChange={(e) => setNewPet({ ...newPet, species: e.target.value })} />
                  </div>
                  <div className="space-y-1">
                    <Label>Race</Label>
                    <Input value={newPet.breed} onChange={(e) => setNewPet({ ...newPet, breed: e.target.value })} />
                  </div>
                  <div className="space-y-1">
                    <Label>Âge (ans)</Label>
                    <Input
                      type="number"
                      value={newPet.age}
                      onChange={(e) => setNewPet({ ...newPet, age: e.target.value })}
                    />
                  </div>
                </div>
                <div className="flex gap-2">
                  <Button
                    type="button"
                    className="flex-1"
                    onClick={async () => {
                      setPetError(null)
                      const token = typeof localStorage !== "undefined" ? localStorage.getItem("access_token") : null
                      if (!token) {
                        setPetError("Connecte-toi d'abord pour ajouter un animal.")
                        return
                      }
                      try {
                        const created = await createPet({
                          name: newPet.name,
                          species: newPet.species,
                          breed: newPet.breed,
                          age: newPet.age ? Number(newPet.age) : undefined,
                        })
                        setPets((prev) => [...prev, created])
                        setSelectedPet(String(created.id))
                        setAddingPet(false)
                        setNewPet({ name: "", species: "dog", breed: "", age: "" })
                      } catch (err) {
                        console.error("Failed to add pet", err)
                        setPetError("Ajout impossible. Vérifie que tu es connecté et réessaie.")
                      }
                    }}
                    disabled={!newPet.name}
                  >
                    Enregistrer l'animal
                  </Button>
                  <Button variant="ghost" type="button" onClick={() => setAddingPet(false)}>
                    Annuler
                  </Button>
                </div>
              </div>
            )}
          </div>
        )}

        {step === 1 && (
          <div className="grid grid-cols-2 gap-3">
            {serviceTypes.map(({ type, description }) => (
              <button
                key={type}
                onClick={() => setServiceType(type)}
                className={`p-4 rounded-xl border-2 transition-all text-left ${
                  serviceType === type ? "border-primary bg-primary/5" : "border-border hover:border-primary/50"
                }`}
              >
                <div
                  className={`w-10 h-10 rounded-lg flex items-center justify-center mb-3 ${
                    serviceType === type ? "bg-primary text-primary-foreground" : "bg-secondary"
                  }`}
                >
                  <ServiceIcon type={type} size={20} />
                </div>
                <p className="font-semibold text-sm">{getServiceLabel(type)}</p>
                <p className="text-xs text-muted-foreground mt-1">{description}</p>
              </button>
            ))}
          </div>
        )}

        {step === 2 && (
          <div className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="start">Date et heure de début</Label>
              <Input id="start" type="datetime-local" value={dateStart} onChange={(e) => setDateStart(e.target.value)} />
            </div>

            <div className="space-y-2">
              <Label htmlFor="end">Date et heure de fin</Label>
              <Input id="end" type="datetime-local" value={dateEnd} onChange={(e) => setDateEnd(e.target.value)} />
            </div>
          </div>
        )}

        {step === 3 && (
          <div className="h-full flex flex-col space-y-4">
            <div className="flex-1 min-h-[300px] rounded-xl overflow-hidden">
              <MapView showRadius={false} />
            </div>

            <div className="flex items-center gap-3 p-3 bg-secondary rounded-xl">
              <MapPin size={20} className="text-primary" />
              <div className="flex-1">
                <p className="font-medium text-sm">{user?.location?.address || "Tunis, Tunisia"}</p>
                <p className="text-xs text-muted-foreground">Touchez la carte pour changer</p>
              </div>
            </div>
          </div>
        )}

        {step === 4 && (
          <div className="space-y-6">
            <div className="text-center">
              <div className="inline-flex items-center gap-1 text-4xl font-bold text-primary">
                <DollarSign size={32} />
                <span>{price[0]}</span>
              </div>
              <p className="text-muted-foreground mt-1">TND</p>
            </div>

            <div className="px-4">
              <Slider value={price} onValueChange={setPrice} min={10} max={200} step={5} className="mb-4" />
              <div className="flex justify-between text-sm text-muted-foreground">
                <span>10 TND</span>
                <span>200 TND</span>
              </div>
            </div>

            <div className="space-y-2">
              <Label htmlFor="description">Notes additionnelles (optionnel)</Label>
              <Textarea
                id="description"
                placeholder="Instructions pour le gardien..."
                value={description}
                onChange={(e) => setDescription(e.target.value)}
                rows={3}
              />
            </div>
          </div>
        )}

        {step === 5 && (
          <Card>
            <CardContent className="p-4 space-y-4">
              <div className="flex items-center gap-3">
                <Avatar className="h-12 w-12">
                  <AvatarImage src={selectedPetData?.image || "/placeholder.svg"} />
                  <AvatarFallback>{selectedPetData?.name[0]}</AvatarFallback>
                </Avatar>
                <div>
                  <p className="font-semibold">{selectedPetData?.name}</p>
                  <p className="text-sm text-muted-foreground">{selectedPetData?.breed}</p>
                </div>
              </div>

              <div className="h-px bg-border" />

              <div className="flex items-center gap-3">
                <div className="w-10 h-10 bg-primary/10 rounded-lg flex items-center justify-center">
                  <ServiceIcon type={serviceType!} className="text-primary" />
                </div>
                <div>
                  <p className="font-medium">{getServiceLabel(serviceType!)}</p>
                  <p className="text-sm text-muted-foreground">Type de service</p>
                </div>
              </div>

              <div className="flex items-center gap-3">
                <div className="w-10 h-10 bg-primary/10 rounded-lg flex items-center justify-center">
                  <Calendar size={20} className="text-primary" />
                </div>
                <div>
                  <p className="font-medium">
                    {new Date(dateStart).toLocaleDateString()} - {new Date(dateEnd).toLocaleDateString()}
                  </p>
                  <p className="text-sm text-muted-foreground">Plage de dates</p>
                </div>
              </div>

              <div className="flex items-center gap-3">
                <div className="w-10 h-10 bg-primary/10 rounded-lg flex items-center justify-center">
                  <MapPin size={20} className="text-primary" />
                </div>
                <div>
                  <p className="font-medium">{user?.location?.address}</p>
                  <p className="text-sm text-muted-foreground">Localisation</p>
                </div>
              </div>

              <div className="flex items-center gap-3">
                <div className="w-10 h-10 bg-accent/10 rounded-lg flex items-center justify-center">
                  <DollarSign size={20} className="text-accent" />
                </div>
                <div>
                  <p className="font-medium text-accent">{price[0]} TND</p>
                  <p className="text-sm text-muted-foreground">Ton offre</p>
                </div>
              </div>

              {description && (
                <>
                  <div className="h-px bg-border" />
                  <p className="text-sm text-muted-foreground">{description}</p>
                </>
              )}
            </CardContent>
          </Card>
        )}
      </div>

      <div className="p-4 border-t border-border">
        <Button onClick={handleNext} className="w-full" size="lg" disabled={!canContinue() || submitting}>
          {step === STEPS.length - 1 ? "Envoyer la demande" : "Continuer"}
        </Button>
      </div>
    </div>
  )
}
