"use client"

import { useState } from "react"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Button } from "@/components/ui/button"
import { Card, CardContent } from "@/components/ui/card"
import { Switch } from "@/components/ui/switch"
import { TrustBadge } from "@/components/ui/trust-badge"
import { ArrowLeft, Star, Edit2, Camera } from "lucide-react"
import type { User } from "@/app/page"
import { useI18n } from "@/lib/i18n"

interface SitterProfileProps {
  user: User
  onBack: () => void
  onUpdateUser: (updates: Partial<User>) => void
}

const mockReviews = [
  {
    id: "rev1",
    reviewerName: "Ahmed Ben Ali",
    reviewerAvatar: "/thoughtful-man-portrait.png",
    rating: 5,
    comment: "Sarah a été incroyable avec Caramel ! Très professionnelle et attentionnée.",
    createdAt: "2024-01-10",
  },
  {
    id: "rev2",
    reviewerName: "Fatma Trabelsi",
    reviewerAvatar: "/woman-portrait.png",
    rating: 5,
    comment: "Service excellent ! Mon chat était heureux quand je l’ai récupéré.",
    createdAt: "2024-01-05",
  },
  {
    id: "rev3",
    reviewerName: "Karim Mansour",
    reviewerAvatar: "/young-man-portrait.png",
    rating: 4,
    comment: "Bonne communication et belles promenades avec Rocky.",
    createdAt: "2024-01-01",
  },
]

export function SitterProfile({ user, onBack, onUpdateUser }: SitterProfileProps) {
  const { t } = useI18n()
  const [isAvailable, setIsAvailable] = useState(user?.isAvailable ?? true)

  const handleAvailabilityChange = (checked: boolean) => {
    setIsAvailable(checked)
    onUpdateUser({ isAvailable: checked })
  }

  return (
    <div className="min-h-screen bg-background flex flex-col">
      <div className="flex items-center gap-4 p-4 border-b border-border">
        <button onClick={onBack} className="p-2 hover:bg-secondary rounded-lg">
          <ArrowLeft size={20} />
        </button>
        <div className="flex-1">
          <h1 className="font-semibold">Mon profil</h1>
        </div>
        <Button variant="ghost" size="icon">
          <Edit2 size={18} />
        </Button>
      </div>

      <div className="flex-1 overflow-y-auto">
        <div className="p-6 flex flex-col items-center text-center">
          <div className="relative mb-4">
            <Avatar className="h-24 w-24">
              <AvatarImage src={user?.avatar || "/placeholder.svg"} />
              <AvatarFallback className="text-2xl">{user?.name?.[0]}</AvatarFallback>
            </Avatar>
            <button className="absolute bottom-0 right-0 w-8 h-8 bg-primary rounded-full flex items-center justify-center text-primary-foreground shadow-md">
              <Camera size={14} />
            </button>
          </div>

          <h2 className="text-xl font-semibold">{user?.name}</h2>
          <p className="text-muted-foreground mb-3">{user?.email}</p>

          <TrustBadge score={user?.trustScore ?? 0} reviewCount={user?.reviewCount} size="lg" />
        </div>

        <div className="px-4 space-y-4 pb-8">
          <Card>
            <CardContent className="p-4 flex items-center justify-between">
              <div>
                <p className="font-medium">Disponibilité</p>
                <p className="text-sm text-muted-foreground">
                  {isAvailable ? "Visible pour les propriétaires" : "Vous ne recevrez pas de nouvelles demandes"}
                </p>
              </div>
              <Switch checked={isAvailable} onCheckedChange={handleAvailabilityChange} />
            </CardContent>
          </Card>

          <Card>
            <CardContent className="p-4">
              <h3 className="font-medium mb-2">À propos</h3>
              <p className="text-sm text-muted-foreground">
                {user?.bio || "Ajoutez une bio pour rassurer les propriétaires..."}
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardContent className="p-4">
              <h3 className="font-medium mb-3">Services proposés</h3>
              <div className="flex flex-wrap gap-2">
                {user?.servicesOffered?.map((service) => (
                  <span
                    key={service}
                    className="px-3 py-1.5 bg-primary/10 text-primary rounded-full text-sm font-medium"
                  >
                    {service}
                  </span>
                )) || <p className="text-sm text-muted-foreground">Aucun service ajouté pour l'instant</p>}
              </div>
            </CardContent>
          </Card>

          <div className="grid grid-cols-3 gap-3">
            <Card>
              <CardContent className="p-4 text-center">
                <p className="text-2xl font-bold text-primary">{user?.reviewCount || 0}</p>
                <p className="text-xs text-muted-foreground">{t("reviewsCount")}</p>
              </CardContent>
            </Card>
            <Card>
              <CardContent className="p-4 text-center">
                <p className="text-2xl font-bold text-primary">{user?.trustScore?.toFixed(1) || "0.0"}</p>
                <p className="text-xs text-muted-foreground">{t("avgRating")}</p>
              </CardContent>
            </Card>
            <Card>
              <CardContent className="p-4 text-center">
                <p className="text-2xl font-bold text-accent">23</p>
                <p className="text-xs text-muted-foreground">Terminées</p>
              </CardContent>
            </Card>
          </div>

          <div>
            <div className="flex items-center justify-between mb-3">
              <h3 className="font-semibold">Avis</h3>
              <Button variant="ghost" size="sm">
                Voir tout
              </Button>
            </div>

            <div className="space-y-3">
              {mockReviews.map((review) => (
                <Card key={review.id}>
                  <CardContent className="p-4">
                    <div className="flex items-start gap-3">
                      <Avatar className="h-10 w-10">
                        <AvatarImage src={review.reviewerAvatar || "/placeholder.svg"} />
                        <AvatarFallback>{review.reviewerName[0]}</AvatarFallback>
                      </Avatar>
                      <div className="flex-1">
                        <div className="flex items-center justify-between mb-1">
                          <p className="font-medium text-sm">{review.reviewerName}</p>
                          <div className="flex items-center gap-1">
                            {Array.from({ length: review.rating }).map((_, i) => (
                              <Star key={i} size={12} className="fill-yellow-400 text-yellow-400" />
                            ))}
                          </div>
                        </div>
                        <p className="text-sm text-muted-foreground">{review.comment}</p>
                        <p className="text-xs text-muted-foreground mt-1">
                          {new Date(review.createdAt).toLocaleDateString()}
                        </p>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
