"use client"

import type React from "react"
import { useEffect, useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { PawPrint, User, Briefcase, ArrowLeft, Loader2, X } from "lucide-react"

type UserRole = "owner" | "sitter"
type AuthMode = "login" | "register" | "role-select"

interface AuthModalProps {
  isOpen: boolean
  onClose: () => void
  initialMode: "login" | "register"
  onLogin: (email: string, password: string) => Promise<void>
  onRegister: (name: string, email: string, password: string, role: UserRole) => Promise<void>
  authError?: string
}

export function AuthModal({ isOpen, onClose, initialMode, onLogin, onRegister, authError }: AuthModalProps) {
  const [mode, setMode] = useState<AuthMode>(initialMode === "register" ? "role-select" : "login")
  const [selectedRole, setSelectedRole] = useState<UserRole | null>(null)
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [name, setName] = useState("")
  const [isLoading, setIsLoading] = useState(false)

  useEffect(() => {
    if (isOpen) {
      setMode(initialMode === "register" ? "role-select" : "login")
      setSelectedRole(null)
      setEmail("")
      setPassword("")
      setName("")
    }
  }, [isOpen, initialMode])

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)
    try {
      await onLogin(email, password)
      onClose()
    } finally {
      setIsLoading(false)
    }
  }

  const handleRegister = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!selectedRole) return
    setIsLoading(true)
    try {
      await onRegister(name, email, password, selectedRole)
      onClose()
    } finally {
      setIsLoading(false)
    }
  }

  const handleRoleSelect = (role: UserRole) => {
    setSelectedRole(role)
    setMode("register")
  }

  if (!isOpen) return null

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center">
      <div className="absolute inset-0 bg-foreground/50 backdrop-blur-sm" onClick={onClose} />

      <div className="relative bg-card rounded-2xl shadow-2xl w-full max-w-md mx-4 overflow-hidden border border-border">
        <button
          onClick={onClose}
          className="absolute top-4 right-4 w-8 h-8 rounded-full bg-secondary flex items-center justify-center text-muted-foreground hover:text-foreground transition-colors z-10"
        >
          <X className="w-4 h-4" />
        </button>

        <div className="pt-8 pb-4 px-6 text-center border-b border-border bg-secondary/30">
          <div className="flex items-center justify-center gap-2 mb-2">
            <div className="w-10 h-10 bg-primary rounded-xl flex items-center justify-center">
              <PawPrint className="w-6 h-6 text-primary-foreground" />
            </div>
            <span className="text-xl font-bold text-foreground">PetCare</span>
          </div>
        </div>

        <div className="p-6">
          {mode === "login" && (
            <>
              <div className="text-center mb-6">
                <h2 className="text-2xl font-bold text-foreground">Connexion</h2>
                <p className="text-muted-foreground mt-1">Connecte-toi pour continuer</p>
              </div>
              <form onSubmit={handleLogin} className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="email">Email</Label>
                  <Input
                    id="email"
                    type="email"
                    placeholder="you@example.com"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                    className="h-12"
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="password">Mot de passe</Label>
                  <Input
                    id="password"
                    type="password"
                    placeholder="••••••••"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                    className="h-12"
                  />
                </div>
                {authError ? <p className="text-sm text-destructive">{authError}</p> : null}
                <Button type="submit" className="w-full h-12 text-base" disabled={isLoading}>
                  {isLoading ? <Loader2 className="w-4 h-4 animate-spin mr-2" /> : null}
                  Se connecter
                </Button>
              </form>

              <div className="mt-6 text-center">
                <p className="text-sm text-muted-foreground">
                  Pas de compte ?{" "}
                  <button onClick={() => setMode("role-select")} className="text-primary hover:underline font-medium">
                    Créer un compte
                  </button>
                </p>
              </div>
            </>
          )}

          {mode === "role-select" && (
            <>
              <div className="text-center mb-6">
                <h2 className="text-2xl font-bold text-foreground">Choisis ton rôle</h2>
                <p className="text-muted-foreground mt-1">Propriétaire ou gardien</p>
              </div>
              <div className="space-y-3">
                <button
                  onClick={() => handleRoleSelect("owner")}
                  className="w-full p-4 rounded-xl border-2 border-border hover:border-primary hover:bg-primary/5 transition-all text-left flex items-start gap-4"
                >
                  <div className="w-12 h-12 bg-primary/10 rounded-xl flex items-center justify-center shrink-0">
                    <User className="w-6 h-6 text-primary" />
                  </div>
                  <div>
                    <h3 className="font-semibold text-foreground">Propriétaire</h3>
                    <p className="text-sm text-muted-foreground">Trouve un gardien de confiance</p>
                  </div>
                </button>

                <button
                  onClick={() => handleRoleSelect("sitter")}
                  className="w-full p-4 rounded-xl border-2 border-border hover:border-accent hover:bg-accent/5 transition-all text-left flex items-start gap-4"
                >
                  <div className="w-12 h-12 bg-accent/10 rounded-xl flex items-center justify-center shrink-0">
                    <Briefcase className="w-6 h-6 text-accent" />
                  </div>
                  <div>
                    <h3 className="font-semibold text-foreground">Gardien</h3>
                    <p className="text-sm text-muted-foreground">Gagne en gardant des animaux</p>
                  </div>
                </button>
              </div>

              <div className="mt-6 text-center">
                <button onClick={() => setMode("login")} className="text-sm text-muted-foreground hover:text-foreground">
                  Déjà un compte ? Connexion
                </button>
              </div>
            </>
          )}

          {mode === "register" && (
            <>
              <button
                onClick={() => setMode("role-select")}
                className="flex items-center gap-1 text-sm text-muted-foreground hover:text-foreground mb-4"
              >
                <ArrowLeft size={16} />
                Retour
              </button>
              <div className="text-center mb-6">
                <h2 className="text-2xl font-bold text-foreground">Créer un compte</h2>
                <p className="text-muted-foreground mt-1">
                  Inscription en tant que {selectedRole === "owner" ? "propriétaire" : "gardien"}
                </p>
              </div>
              <form onSubmit={handleRegister} className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="name">Nom complet</Label>
                  <Input
                    id="name"
                    type="text"
                    placeholder="Jean Dupont"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    required
                    className="h-12"
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="reg-email">Email</Label>
                  <Input
                    id="reg-email"
                    type="email"
                    placeholder="you@example.com"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                    className="h-12"
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="reg-password">Mot de passe</Label>
                  <Input
                    id="reg-password"
                    type="password"
                    placeholder="••••••••"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                    className="h-12"
                  />
                </div>
                {authError ? <p className="text-sm text-destructive">{authError}</p> : null}
                <Button type="submit" className="w-full h-12 text-base" disabled={isLoading}>
                  {isLoading ? <Loader2 className="w-4 h-4 animate-spin mr-2" /> : null}
                  S’inscrire
                </Button>
              </form>
            </>
          )}
        </div>
      </div>
    </div>
  )
}
