"use client"

import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Button } from "@/components/ui/button"
import { Card, CardContent } from "@/components/ui/card"
import { StatusChip } from "@/components/ui/status-chip"
import { ServiceIcon, getServiceLabel } from "@/components/ui/service-icon"
import { MapView } from "@/components/map/map-view"
import type { Booking } from "@/lib/types"
import { ArrowLeft, Phone, MessageCircle, MapPin, Calendar, Star } from "lucide-react"
import { format } from "date-fns"

interface ActiveBookingProps {
  booking: Booking
  onBack: () => void
}

export function ActiveBooking({ booking, onBack }: ActiveBookingProps) {
  return (
    <div className="min-h-screen bg-background flex flex-col">
      <div className="flex items-center gap-4 p-4 border-b border-border">
        <button onClick={onBack} className="p-2 hover:bg-secondary rounded-lg">
          <ArrowLeft size={20} />
        </button>
        <div className="flex-1">
          <h1 className="font-semibold">Réservation active</h1>
          <StatusChip status={booking.status} size="sm" />
        </div>
      </div>

      <div className="h-48 relative">
        <MapView
          markers={[
            {
              id: booking.sitterId,
              lat: booking.location.lat,
              lng: booking.location.lng,
              type: "sitter",
              label: booking.sitterName,
            },
          ]}
        />
      </div>

      <div className="flex-1 p-4 overflow-y-auto space-y-4">
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <Avatar className="h-14 w-14">
                <AvatarImage src={booking.sitterAvatar || "/placeholder.svg"} />
                <AvatarFallback>{booking.sitterName[0]}</AvatarFallback>
              </Avatar>
              <div className="flex-1">
                <p className="font-semibold">{booking.sitterName}</p>
                <p className="text-sm text-muted-foreground">S'occupe de {booking.petName}</p>
              </div>
              <div className="flex gap-2">
                <Button variant="outline" size="icon" className="rounded-full bg-transparent">
                  <Phone size={18} />
                </Button>
                <Button variant="outline" size="icon" className="rounded-full bg-transparent">
                  <MessageCircle size={18} />
                </Button>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-4 space-y-3">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-primary/10 rounded-lg flex items-center justify-center">
                <ServiceIcon type={booking.serviceType} className="text-primary" />
              </div>
              <div>
                <p className="font-medium">{getServiceLabel(booking.serviceType)}</p>
                <p className="text-sm text-muted-foreground">Service</p>
              </div>
            </div>

            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-primary/10 rounded-lg flex items-center justify-center">
                <Calendar size={20} className="text-primary" />
              </div>
              <div>
                <p className="font-medium">
                  {format(new Date(booking.dateStart), "MMM d")} - {format(new Date(booking.dateEnd), "MMM d, yyyy")}
                </p>
                <p className="text-sm text-muted-foreground">Durée</p>
              </div>
            </div>

            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-primary/10 rounded-lg flex items-center justify-center">
                <MapPin size={20} className="text-primary" />
              </div>
              <div>
                <p className="font-medium">{booking.location.address}</p>
                <p className="text-sm text-muted-foreground">Localisation</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <div>
          <h3 className="font-semibold mb-3">Mises à jour</h3>
          <div className="space-y-4">
            {booking.updates.map((update, index) => (
              <div key={update.id} className="flex gap-3">
                <div className="flex flex-col items-center">
                  <div className="w-3 h-3 bg-primary rounded-full" />
                  {index < booking.updates.length - 1 && <div className="w-0.5 flex-1 bg-border mt-1" />}
                </div>
                <div className="flex-1 pb-4">
                  <p className="text-xs text-muted-foreground mb-1">
                    {format(new Date(update.timestamp), "MMM d, h:mm a")}
                  </p>
                  <p className="text-sm">{update.message}</p>
                  {update.image && (
                    <img
                      src={update.image || "/placeholder.svg"}
                      alt="Update"
                      className="mt-2 rounded-lg w-full max-w-xs"
                    />
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>

      {booking.status === "in-progress" && (
        <div className="p-4 border-t border-border">
          <Button className="w-full bg-transparent" variant="outline">
            <Star size={18} className="mr-2" />
            Laisser un avis après la fin
          </Button>
        </div>
      )}
    </div>
  )
}
