import { Home, Footprints, Car, AlertTriangle } from "lucide-react"
import type { ServiceType } from "@/lib/types"
import { cn } from "@/lib/utils"

interface ServiceIconProps {
  type: ServiceType
  size?: number
  className?: string
}

export function ServiceIcon({ type, size = 20, className }: ServiceIconProps) {
  const icons = {
    sitting: Home,
    walking: Footprints,
    taxi: Car,
    emergency: AlertTriangle,
  }

  const Icon = icons[type]
  return <Icon size={size} className={cn(className)} />
}

export function getServiceLabel(type: ServiceType): string {
  const labels: Record<ServiceType, string> = {
    sitting: "Garde à domicile",
    walking: "Promenade",
    taxi: "Taxi animaux",
    emergency: "Urgence",
  }
  return labels[type] ?? type
}
